<template>
    <div
        class="goods-detail"
        :style="{
            borderTopWidth: `${statusBarHeight}px`,
        }"
    >
        <div class="top-banner">
            <div
                class="close"
                :style="{
                    top: `${statusBarHeight + (18 / devicePixelRatio) }px`,
                }"
                @click="$router.go(-1)"
            />
            <van-swipe
                class="swipe"
                :autoplay="0"
                :show-indicators="false"
            >
                <van-swipe-item
                    class="swipe-item"
                    v-for="(img, i) in imgs"
                    :key="i"
                >
                    <img
                        class="banner-item"
                        :src="img"
                    >
                </van-swipe-item>
            </van-swipe>
            <div class="info">
                <div class="info__title">
                    <div>券后价</div>
                    <div>已拼{{ detail.salesTip || 0 }}件</div>
                </div>
                <div class="info__price">
                    <span>{{ (detail.originalPrice - detail.discount || 0).toFixed(1) }}</span>
                    <s>¥{{ detail.originalPrice || 0 }}</s>
                </div>
            </div>
        </div>
        <div class="goods-info">
            <div class="goods-title Hairline Hairline_bottom">{{ detail.name }}</div>
            <p class="goods-tip">全场包邮 · 7天退换 · 假一赔十</p>
        </div>
        <div class="detail">
            <h3 class="detail__title">商品详情</h3>
            <img
                class="detail__img"
                v-for="(img, i) in imgs"
                :key="`detail-${i}`"
                :src="img">
        </div>
        <div class="footer">
            <button
                class="footer__btn"
                @click="gotoPDD(detail.linkUrl)"
            >
                立即抢券省购 >
            </button>
        </div>
    </div>
</template>

<script>
import { getStatusBarHeight, openCommonWebView } from '@/utils/jsApiV2/client';
import { isXiangkan } from '@/utils/ua';
import { getGoodsDetail } from '@/api/pddMall';

export default {
    name: 'PDDMallGoodsDetail',
    data() {
        return {
            detail: {},
            statusBarHeight: getStatusBarHeight() / window.devicePixelRatio,
            devicePixelRatio: window.devicePixelRatio,
        };
    },
    computed: {
        imgs() {
            if (this.detail.goodsGalleryUrls) {
                const ret = [];
                try {
                    ret.push(...JSON.parse(this.detail.goodsGalleryUrls));
                } catch (_) {
                    console.error('pdd详情页图片列表json异常');
                }
                return ret;
            }
            return [];
        },
    },
    methods: {
        gotoPDD(url) {
            this.$o2o({
                e_c: '立即购买',
                e_a: '点击',
                e_n: 'PDD商品详情页',
            });
            if (isXiangkan()) {
                openCommonWebView(url, 'pdd');
            } else {
                location.href = url;
            }
        },
    },
    created() {
        this.$root.showPlaceholder();
        getGoodsDetail({
            goodsId: this.$route.query.goodsId,
        }).then((ret) => {
            this.detail = ret instanceof Array ? ret[0] : ret;
            this.$root.hidePlaceholder();
            this.$o2o({
                e_c: '详情页',
                e_a: '曝光',
                e_n: 'PDD商品详情页',
            });
        }, this.$error);
    },
};
</script>

